<template>
    <div class="courseDetail">
        <div class="searchList" v-if="!searchDetail">
            <h1>{{courseInfo.name}}</h1>
            <el-table
                :data="[courseInfo]"
                style="width: 100%"
                border>
                <el-table-column 
                    label="学期授课资源"
                    align="center">
                    <el-table-column 
                        label="教学文档"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                            @click="downLoad('teachText',1)" 
                            type="text" 
                            :disabled="courseInfo.teachText=='无'?true:false" 
                            size="small">{{courseInfo.teachText}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="教学案例"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                            @click="downLoad('teachDemo',2)" 
                            type="text" 
                            :disabled="courseInfo.teachDemo=='无'?true:false" 
                            size="small">{{courseInfo.teachDemo}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="教学资源"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                            @click="downLoad('teachResourse',3)" 
                            :disabled="courseInfo.teachResourse=='无'?true:false" 
                            type="text" 
                            size="small">{{courseInfo.teachResourse}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="其他"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="downLoad('teachOthers',4)" 
                                :disabled="courseInfo.teachOthers=='无'?true:false" 
                                type="text" 
                                size="small">{{courseInfo.teachOthers}}</el-button>
                        </template>
                    </el-table-column>
                </el-table-column>
                <el-table-column
                    label=""
                    width="2px"
                ></el-table-column>
                <el-table-column 
                    label="学期作业文档"
                    align="center">
                    <el-table-column 
                        label="实验报告模板"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="downLoad('workReport',5)" 
                                :disabled="courseInfo.workReport=='无'?true:false" 
                                type="text" 
                                size="small">{{courseInfo.workReport}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="平时作业模板"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="downLoad('workExam',6)" 
                                :disabled="courseInfo.workExam=='无'?true:false" 
                                type="text" 
                                size="small">{{courseInfo.workExam}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="其他"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="downLoad('workOthers',7)" 
                                :disabled="courseInfo.workOthers=='无'?true:false" 
                                type="text" 
                                size="small">{{courseInfo.workOthers}}</el-button>
                        </template>
                    </el-table-column>
                </el-table-column>
                </el-table-column>
            </el-table>
        </div>
        <FileDownload v-if="searchDetail" @back="searchDetail=false" :type="type"></FileDownload>
    </div>
</template>

<script>
import FileDownload from './fileDownload.vue'
import {mapGetters,mapActions} from 'vuex'
export default {
    data(){
        return{
            searchDetail:false,
            type:0
        }
    },
    props:["chooseContent"],
    components:{
        FileDownload
    },
    computed:{
        ...mapGetters("stu",{
            courseInfo:"getCourseInfo"
        })
    },
    methods:{
        ...mapActions('stu',['requestDetail']),
        downLoad(text,type){
            this.requestDetail({id:this.chooseContent.id,type});
            this.type=type;
            this.searchDetail=true;
        }
    }
}
</script>

<style lang="less" scoped>

</style>
