<template>
    <div class="fileDownload">
        <h1 class="courseName">{{courseInfo.name}}</h1>
        <span class="source">{{type | parentName}}>{{type | childName}}</span>
        <div class="handleBtn">
            <el-button type="primary" round >下载</el-button>
            <el-button type="info" round @click="$emit('back')">返回</el-button>
        </div>
        <div class="downloadDetail">
            <el-table
                ref="multipleTable"
                :data="detailFile"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="">
                <el-table-column
                    type="selection"
                    width="55">
                </el-table-column>
                <el-table-column
                    prop="name"
                    label="教学文档"
                    width="600"
                    align="center">
                </el-table-column>
                <el-table-column
                    label="操作"
                    align="center">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">下载</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import {mapGetters} from 'vuex'

export default {
    computed:{
        ...mapGetters("stu",{
            courseInfo:"getCourseInfo",
            detailFile:"getDetailFile"
        })
    },
    props:["type"],
    filters:{
        parentName:function(val){
            if(val<=4){
                return '学期授课资源'
            }else{
                return '学期作业文档'
            }
        },
        childName:function(val){
            let arr=['教学文档','教学案例','教学资源','其他','实验报告模板','平时作业模板','其他'];
            return arr[val+1];
        }
    }
}
</script>

<style lang="less" scoped>
.fileDownload{
    .courseName{
        font-size: 18px;
    }
    .handleBtn{
        float: right;
    }
}
</style>
