<template>
    <div class="newCourse">
        <el-form :inline="true" class="demo-form-inline">
            <el-form-item label="课程名称">
                <el-input v-model="course" placeholder=""></el-input>
            </el-form-item>
            <el-button type="primary" @click="create()">确定创建</el-button>
        </el-form>
    </div>
</template>

<script>
import {mapActions} from 'vuex'
export default {
    data(){
        return{
            course:""
        }
    },
    methods:{
        ...mapActions('teach',["createCourse"]),
        create(){
            if(this.course!==""){
                this.$confirm('是否确定创建新课程?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.$message({
                            type: 'success',
                            message: '创建成功!'
                        });
                        this.createCourse(this.course);
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消创建'
                        });          
                });
            }
        }
    }
}
</script>

