const namespaced = true;
import axios from 'axios';
const state = {
    courseInfo: {
        name: "java程序设计基础",
        teachText: '下载',
        teachDemo: '无',
        teachResourse: '下载',
        teachOthers: '下载',
        workReport: '下载',
        workExam: '下载',
        workOthers: '无'
    },
    searchContent:[{
        id:1,
        teacher:'寒冬',
        course:'计算机网络'
    },{
        id:2,
        teacher:'寒冬',
        course:'数据结构'
    }],
    detailFile: [{
        name: "作业1",
        src: ""
    }],
    updateTable:[{
        id:'',
        name:'实验报告一',
    },{
        id:'',
        name:'实验报告二',
    },{
        id:'',
        name:'实验报告二',
    },{
        id:'',
        name:'实验报告二',
    }]
}

const getters = {
    getCourseInfo(state) {
        return state.courseInfo
    },
    getDetailFile(state){
        return state.detailFile
    },
    getSearchContent(state){
        return state.searchContent
    },
    getUpdateTable(state){
        return state.updateTable
    }
}

const mutations = {
    changeInfo(state, data) {
        state.courseInfo = data;
    }
}

const actions = {
    // 请求上传的文件信息
    requestUpdateTable({commit},{teacher,course,classNo}){
        axios.get(`url?teacher=${teacher}&course=${course}&class=${classNo}`)
            .then((res) => {
                commit('changeInfo', res.data)
            }).catch((err) => {
                console.log(`the error happen because of ${err}`)
            })
    },
    // 请求课程文档信息
    requestInfo({ commit }, { teacher, course }) {
        axios.get(`url?teacher=${teacher}&course=${course}`)
            .then((res) => {
                commit('changeInfo', res.data)
            }).catch((err) => {
                console.log(`the error happen because of ${err}`)
            })
    },
    // 请求课程具体文档目录
    requestDetail({commit},{id,type}){
        axios.get(`url?id=${id}&type=${type}`)
            .then(res=>{
                commit('changeDetail',res.data);
            }).catch(err=>{

            })
    }
}
export default { state, getters, mutations, actions, namespaced }